<template>
  <div class="changeQuantity">
    <headerUsually btnShow="true" titleText="更改数量" url="/mobileOrdering/SDishOperation"></headerUsually>
    <div class="content">
      <div class="item people-num">
        <div class="left">数量</div>
        <div class="right">
          <i class="iconfont icon-jian" @click="cutBackFun()"></i>
          <!-- <span class="num">{{quantity}}</span> -->
          <input class="num" type="text" v-model="quantity" />
          <i class="iconfont icon-jia2" @click="addFun()"></i>
        </div>
      </div>
    </div>
    <div class="btn" @click="okFun()">确定</div>
  </div>
</template>
<script>
import ajaxGet from "@/conf/ajaxGet";
import { Toast } from "mint-ui";
import headerUsually from "@/components/common/mobileOrdering/headerUsually";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    headerUsually
  },
  data() {
    return {
      quantity: -1,
      newModifySDish: {},
      originalNumber: 0
    };
  },
  computed: {
    ...mapState({
      modifySDish: state => state.mobileOrdering.modifySDish
    })
  },
  created() {
    this.newModifySDish = _.cloneDeep(this.modifySDish);
    this.quantity = this.newModifySDish.number;
    console.log("this.newModifySDish:", this.newModifySDish);
  },
  methods: {
    addFun() {
      this.quantity++;
    },
    cutBackFun() {
      if (this.quantity > 1) {
        this.quantity--;
      }
    },
    async okFun() {
      if (this.newModifySDish.accnt) {
        let data = {
          params: {
            proType: 1,
            pluState: this.newModifySDish.pluState,
            accnt: this.newModifySDish.accnt,
            orderId: this.newModifySDish.id,
            num: this.quantity
          }
        };
        let res = await ajaxGet("/pos/router?method=updateCheckDetail", data);
        if (res && res.resultCode == 0) {
          //this.$router.push("/mobileOrdering/myOrder");
          if (this.newModifySDish.pluCondDtoList) {
            this.newModifySDish.pluCondDtoList.forEach(ele => {
              ele.descriptEn = "";
              ele.unit = "";
              ele.number = this.quantity;
            });

            let data2 = {
              params: {
                proType: 3,
                pluState: this.newModifySDish.pluState,
                accnt: this.newModifySDish.accnt,
                orderId: this.newModifySDish.id,
                cook: this.newModifySDish.cook,
                codes: this.newModifySDish.condCode,
                remark: this.newModifySDish.remark,
                cookList: JSON.stringify(this.newModifySDish.pluCondDtoList)
              }
            };

            let res2 = await ajaxGet(
              "/pos/router?method=updateCheckDetail",
              data2
            );
            if (res2 && res2.resultCode == 0) {
              this.$router.push("/mobileOrdering/myOrder");
            }
          } else {
            this.$router.push("/mobileOrdering/myOrder");
          }
        }
      } else {
        this.newModifySDish.amount =
          (Number.parseFloat(this.newModifySDish.amount) /
            Number.parseFloat(this.newModifySDish.number)) *
          this.quantity;
        this.newModifySDish.number = this.quantity;
        //this.$store.commit("submitModifySDish", this.newModifySDish);
        this.$store.commit("changeTempTable", {
          type: "change",
          item: this.newModifySDish
        });

        this.$router.push("/mobileOrdering/myOrder");
      }
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "middle",
        duration: 3000
      });
    }
  },
  watch: {
    quantity() {
      //console.log("val:", val);
      this.quantity = this.quantity.toString();
      if (Number(this.quantity) > 100) {
        this.quantity = this.originalNumber;
        return;
      }
      this.quantity = this.quantity.replace(/[^\d.]/g, ""); //清除“数字”和“.”以外的字符
      this.quantity = this.quantity.replace(/\.{2,}/g, "."); //只保留第一个. 清除多余的
      this.quantity = this.quantity
        .replace(".", "$#$")
        .replace(/\./g, "")
        .replace("$#$", ".");
      this.quantity = this.quantity.replace(
        /^(\-)*(\d+)\.(\d\d).*$/,
        "$1$2.$3"
      ); //只能输入两个小数
      if (this.quantity.indexOf(".") < 0 && this.quantity != "") {
        //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
        this.quantity = parseFloat(this.quantity).toString();
      }
      this.originalNumber = this.quantity;
    }
  }
};
</script>
<style lang="less" scoped>
.changeQuantity {
  padding-top: 46px;
  .content {
    padding-left: 15px;
    background-color: #fff;
    .item {
      height: 70px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #ddd;
      padding-right: 15px;
      .left {
        font-size: 14px;
        color: #666666;
      }
      .right {
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #333333;
        .iconfont {
          color: #a8acb8;
        }
      }
    }
    .item:last-child {
      border-bottom: none;
    }
    .people-num {
      .right {
        .iconfont {
          color: #c4ad68;
          font-size: 30px;
        }
        .num {
          //margin: 0 20px;
          width: 50px;
          text-align: center;
          border: none;
          outline: none;
        }
      }
    }
  }
  .btn {
    margin-top: 20px;
    background: #ffffff;
    font-size: 18px;
    color: #c4ad68;
    text-align: center;
    padding: 11px;
  }
}
</style>



